@import url(https://fonts.googleapis.com/css?family=Lobster|Source+Sans+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.signUp .navbar{
    background-color: #f5f6fa;
    margin-bottom: 5px;
}

.signUp .navbar-brand img{
    position: relative;
    width: 200px;
    left: 85px;
}


.signUp .container-fluid{
    background-color: var(--primary-color);
    height:700px;
}

.signUp .btn-group{
    margin-top: 30px;
    margin-bottom: 50px;
}

.signUp .form-group{
    margin-bottom: 30px;
}

.signUp .form-group input{
    margin-top: -1px;
}

.signUp .btn-search-ticket button{
    margin-top: 30px;
    width: 100%;
}

footer{
    position: relative;
    background-color: #dfe6e9;
    width: 100%;
}

footer .col-4{
    border-right: 1px solid #000000;
    height: 70px;
}

footer .col-4 i{
    margin-top: 6px;
    font-size: 32px;
}

.ticket-plane nav{
    background-color: var(--primary-color);
    margin-bottom: 20px;
}

.ticket-plane nav i{
    color: #ffffff;
    margin-top: 5px;
}

.ticket-plane nav span{
    color: #ffffff;
    font-size: 15px;
    margin-left: 10px;
}

.ticket-plane nav p{
    color: #ffffff;
    font-size: 8px;
    margin-left: 29px;
    margin-bottom: -3px;
}

.ticket-plane .card{
    margin-bottom: 20px;
}

.ticket-plane .inline-ticket-title img, p{
    display: inline;
}

.ticket-plane .inline-ticket-title p{
    font-size: 10px;
    margin-left: 5px;
}

.ticket-plane .card-body img{
    width: 30px;
}

.ticket-plane .card-body .row .col-5 span{
    color: var(--primary-color);
    font-weight: bold;
}

.ticket-plane .card-body .row .col-2 p{
    color: black;
    font-size: 8px;
}

.ticket-plane .card-body .row .col-5 p{
    color: var(--primary-color);
    font-size: 10px;
}

.ticket-plane .card-footer p{
    font-size: 10px;
}

.ticket-plane .card-footer span{
    font-size: 12px;
}

.ticket-detail nav{
    background-color: var(--primary-color);
    margin-bottom: 20px;
}

.ticket-detail nav i{
    color: #ffffff;
    margin-top: 5px;
}

.ticket-detail nav span{
    color: #ffffff;
    font-size: 15px;
    margin-left: 10px;
}

.ticket-detail nav p{
    color: #ffffff;
    font-size: 8px;
    margin-left: 29px;
    margin-bottom: -3px;
}

.ticket-detail .title-ticket{
    background-color: #dfe6e9;
    color: var(--primary-color);
}

.footer-ticket-detail{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #ffffff;
}

:root{
    --primary-color: #4834d4;
    --secondary-color: #ffffff;
}

body{
    font-family: 'Source Sans Pro', sans-serif;
    overflow-x: hidden;
}





